$(function(){
  
  detectPromo();
  
  $(document).scroll(function(){
    
    detectPromo();
    
  });
                     
  function detectPromo(){
    
    if ( $('.promo')[0].getBoundingClientRect().top < $(window).outerHeight() ){
      $('.promo').addClass('promo_visible');
    } else {
      $('.promo').removeClass('promo_visible');
    }
    
  }                   
  
});
